.contacts__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 77vw;
  margin-bottom: 50px;
  margin: 0 auto; }
  .contacts__container .contacts-greeting {
    text-align: center; }
  .contacts__container .contact-input {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 20px; }
  .contacts__container .contacts__text {
    margin-top: 10px; }
  .contacts__container form .button {
    display: block;
    margin: 0 auto;
    margin-top: 20px;
    color: green; }
    .contacts__container form .button:hover {
      color: black;
      background-color: #41ff54; }

.submit-button {
  display: block;
  margin: auto;
  margin-top: 20px;
  background-color: #41ff54;
  color: black; }

@media screen and (max-width: 600px) {
  .contacts__container {
    width: 80vw;
    margin-bottom: 50px; }
    .contacts__container .contact-input-value, .contacts__container .contacts__text, .contacts__container #exampleFormControlTextarea1 {
      width: 300px; }
    .contacts__container form {
      margin-bottom: 20px; }
      .contacts__container form .button {
        padding: 10px 50px;
        margin: 0; }
    .contacts__container .text-area {
      width: 300px; } }
