.page {
  text-align: justify;
  font-family: 'Comfortaa', cursive; }

.list-item {
  margin: 0; }
  .list-item:hover {
    text-decoration: none; }

.activities {
  padding-left: 30px;
  width: 50vw; }
  .activities .heading {
    font-size: 26px;
    background-color: #1aff30;
    border-radius: 25px;
    padding-left: 15px; }
  .activities .point {
    text-decoration: underline; }
  .activities li {
    cursor: default; }

.activity {
  margin-bottom: 30px; }

.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-bottom: 10px; }

.collapsible {
  margin-bottom: 10px; }

.contactButton {
  margin-top: 10px;
  margin-bottom: 16px;
  padding: 5px 15px;
  background: #74ebd5;
  background: -webkit-linear-gradient(to right, #74ebd5, #acb6e5);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #74ebd5, #acb6e5);
  outline: none;
  border: 0;
  border-radius: 20px; }
  .contactButton:hover {
    padding: 5px 17px; }

.secondContactButton {
  margin-bottom: 30px;
  margin-left: 27px; }

@media screen and (max-width: 600px) {
  .activities {
    width: 80vw;
    text-align: left;
    padding-left: 0; }
  .flex {
    margin-bottom: 30px; }
    .flex .heading {
      width: 70vw; } }

@media screen and (max-width: 800px) {
  .flex {
    margin-bottom: 20px; } }
